import { Item, baseItem } from "../baseEntity";
import { Point, Phenomenon, Instruction } from "../..";
import { GameData, EntityData } from "../../game";

export interface OrbOfKnowing extends Item {
  type: "orb-of-knowing";
}

export const orbOfKnowing = (position: Point.Point): OrbOfKnowing => ({
  ...baseItem(position),
  type: "orb-of-knowing",
  phenomena: knowing
});

export const knowing: Phenomenon.Phenomenon = {
  name: Phenomenon.Phenomena.KNOW_DOOR_LOCATION,
  duration: 1,
  fn: (gameData: GameData.GameData): Instruction.Instruction[] =>
    EntityData.byType(gameData.entityData, "exit")
      .map(exit => Point.toIndex(exit.position, gameData.maze.dimension))
      .map(p => Instruction.see([p]))
};

